@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);

*{
    font-family: 'Quicksand', sans-serif;;
}
.coin{
    color:white
}
.coin-container {
    display: flex;
    justify-content: center;
  }
  
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
  }
  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    color:white;
    min-width: 300px;
  }
  
  .coin h1 {
    font-size: 16px;
    width: 150px;
    color:white;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;

  }
  
  .coin-symbol {
    text-transform: uppercase;
    color:white;
  }
  
  .coin-data {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
    color:white;
   
  }
  @media only screen and (max-width: 650px) {
    .coin-row{
      display: block;
      padding:20px;
      padding-left: 100px;
      padding-right: 100px;
    }
  }
  
  
  .coin-price {
    width: 110px;
  }
  
  .coin-volume {
    width: 155px;
  }
  
  .coin-marketcap {
    width: 230px;
  }
  
  .coin-percent {
    width: 100px;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
.flex-container {
    display: flex;
    
  }
  



